.container-books-card {
    background-color:#f6f6f6;
  }
  
  .title-best-sellers {
    font-size: 32px;
    line-height: 1.19;
    letter-spacing: -0.04em;
    text-transform: capitalize;
    color:  #111111;
    margin-bottom: 40px;
  
    @include tablet {
      margin-top: 0;
      font-size: 48px;
      line-height: 1.08;
      letter-spacing: -0.04em;
    }
  }
  
  .title-best-sellers-color {
    color: #4f2ee8;
  }
  
  .title-last-word-color {
    color: #4f2ee8;
  }
  
  .item-category {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.29px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    text-overflow: ellipsis;
    color:  #b4afaf;
    margin-bottom: 18px;
    display: block;
  }
  
  .img-card-book {
    @include small-mobile {
      width: 100%;
      height: 485px;
      margin-bottom: 14px;
    }
  
    @include mobile {
      width: 335px;
      height: 485px;
      margin-bottom: 14px;
    }
  
    @include tablet {
      width: 218px;
      height: 316px;
    }
  
    @include desktop {
      width: 160px;
      height: 228px;
    }
  }
  
  .img-book {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 8px;
  }
  
  .box-text-book-best {
    margin-bottom: 8px;
  
    @include tablet {
      margin-bottom: 20px;
    }
  
    @include desktop {
      margin-bottom: 32px;
    }
  }
  
  .card-book {
    margin-bottom: 8px;
  
    @include tablet {
      margin-bottom: 20px;
    }
  
    @include desktop {
      margin-bottom: 0;
    }
  }
  
  .box-title {
    @include small-mobile {
      width: 320px;
      height: 18px;
      margin-bottom: 4px;
    }
  
    @include mobile {
      width: 335px;
    }
  
    @include tablet {
      width: 218px;
    }
  
    @include desktop {
      width: 160px;
    }
  }
  
  .box-author {
    @include small-mobile {
      width: 320px;
      height: 18px;
    }
  
    @include mobile {
      width: 335px;
    }
  
    @include tablet {
      width: 218px;
    }
  
    @include desktop {
      width: 160px;
    }
  }
  
  .title-book {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.12;
    letter-spacing: -0.04em;
    white-space: nowrap;
    overflow: hidden;
    text-transform: uppercase;
    text-overflow: ellipsis;
    color: var(--main-color);
    margin-bottom: 4px;
  }
  
  .author-book {
    font-style: italic;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.17px;
    letter-spacing: -0.04em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-top: 4px;
    padding-bottom: 4px;
    color:  #b4afaf;
  }
  
  .title-category {
    font-size: 32px;
    line-height: 1.19;
    letter-spacing: -0.04em;
    text-transform: capitalize;
    color: #111111;
    margin-bottom: 40px;
  
    @include tablet {
      margin-top: 0;
      font-size: 48px;
      line-height: 1.08;
      letter-spacing: -0.04em;
    }
  
    @include desktop {
      margin-top: 40px;
    }
  }
  
  .author-book-category {
    font-style: italic;
    font-size: 12px;
    line-height: 1.17px;
    letter-spacing: -0.04em;
    text-overflow: ellipsis;
    color: #b4afaf;
  }
  
  .see-more {
    font-family: inherit;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.01em;
    text-transform: uppercase;
  
    color: #111111;
    box-sizing: border-box;
    width: 124px;
    height: 46px;
    margin-left: auto;
    background:  #f6f6f6;
    border: 2px solid #4f2ee8;

    border-radius: 40px;
  
    &:hover,
    :focus {
      background:#4f2ee8;
      color: #111111;
    }
  
    transition-property: color, background-color;
  
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .box-category {
    @include tablet {
      display: flex;
      gap: 25px;
      justify-content: space-between;
    }
  
    @include desktop {
      gap: 21px;
    }
  }
  
  .top-books {
    display: flex;
    flex-direction: column;
    gap: 40px;
   
  
    @include tablet {
      flex-direction: row;
      flex-wrap: wrap;
      gap: 25px;
      row-gap: 40px;
      justify-content: space-between;
    }
  
    @include desktop {
      gap: 21px;
      row-gap: 40px;  }
  }
  
  .btn-up {
    position: fixed;
    right: 30px;
    bottom: 120px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 54px;
    height: 54px;
    border-radius: 50%;
    border: 5px solid rgba(17, 17, 17, 0.5);
    background-color: #4f2ee8;
    box-shadow: 0 0 0 5px rgba(79, 46, 232, 0.3);
    transition: transform 0.2s ease-in-out;
  
    &:hover,
    :focus {
      transform: scale(1.05);
      background-color:#eac645;
    }
  }
  
  .icon-up {
    width: 24px;
    height: 24px;
  }
  
  .is-hidden-up {
    display: none;
  }
  
  .link-books-render:hover {
    cursor: pointer;
  }